@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.2;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Nunito Sans", sans-serif;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
}

.btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 80px;
}

.btn-blue {
  font-size: 14px;
  font-weight: 900;
  line-height: 17px;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
  width: auto;
  height: 49px;
  padding: 16px 24px;
  border: 2px solid rgb(79, 143, 240);
  background: rgb(79, 143, 240);
}

.btn-blue:hover {
  border: 2px solid rgb(79, 143, 240);
  color: rgb(79, 143, 240);
  background: rgb(255, 255, 255);
}

.btn-white {
  font-size: 14px;
  font-weight: 900;
  line-height: 17px;
  text-transform: uppercase;
  width: auto;
  height: 49px;
  padding: 16px 24px;
  border: 2px solid rgb(230, 230, 230);
  background: rgb(230, 230, 230);
}

.btn-white:hover {
  border: 2px solid rgb(230, 230, 230);
  background: rgb(255, 255, 255);
}

.btn-container__blue-white {
  display: flex;
  gap: 24px;
}
@media screen and (max-width: 999px) {
  .btn-container__blue-white {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 210px;
  }
}

.header-top {
  padding: 16px 0;
  background-color: black;
  color: white;
}

.header-top__container {
  display: flex;
  justify-content: space-between;
}

.header-top__left {
  display: flex;
  justify-content: space-between;
  gap: 24px;
}

.header-top__phone {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
}

.header-top__mail {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
}

.header-top__call {
  display: flex;
  justify-content: flex-end;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  color: rgb(79, 143, 240);
}

.header-content {
  background-image: url("/img/фон.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media screen and (max-width: 999px) {
  .header-content {
    background-size: auto;
  }
}

.header-nav__main {
  position: relative;
}

.header-nav {
  padding: 22px 0;
  background: rgba(1, 1, 1, 0.76);
}

.header-nav__container {
  display: flex;
  justify-content: space-between;
}

.header-nav__logo-blue {
  font-size: 28px;
  font-weight: 900;
  text-transform: uppercase;
  color: rgb(79, 143, 240);
}

.header-nav__logo-white {
  font-size: 20px;
  font-weight: 900;
  text-transform: lowercase;
  color: rgb(255, 255, 255);
}

.header-nav__menu {
  display: flex;
  justify-content: space-around;
  gap: 32px;
}
@media screen and (max-width: 999px) {
  .header-nav__menu {
    display: none;
  }
}

.header-nav__btn {
  display: none;
}
@media screen and (max-width: 999px) {
  .header-nav__btn {
    display: block;
  }
}

.header-nav__menu-link {
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  color: white;
}

a:not(.header-nav__menu-link) {
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  color: rgb(79, 143, 240);
}

.header-content__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 150px 0 192px 0;
}
@media screen and (max-width: 999px) {
  .header-content__info {
    padding: 60px 0;
    row-gap: 8px;
  }
}

.header-content__title-white {
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 8px;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}

.header-content__title-blue {
  font-size: 56px;
  font-weight: 600;
  margin-bottom: 8px;
  text-transform: uppercase;
  color: rgb(79, 143, 240);
}

.header-content__text {
  font-size: 20px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 48px;
  color: rgb(255, 255, 255);
}
@media screen and (max-width: 999px) {
  .header-content__text {
    margin-bottom: 32px;
  }
}

.header-nav__slider {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 36px;
}
@media screen and (max-width: 999px) {
  .header-nav__slider {
    display: none;
  }
}

.header-nav__line {
  display: flex;
  align-items: flex-end;
  gap: 8px;
}

.header-nav__line-blue {
  width: 4px;
  height: 40px;
  background: rgb(79, 143, 240);
}

.header-nav__line-white {
  width: 4px;
  height: 24px;
  background: rgb(230, 230, 230);
}

.header-nav__arrow {
  display: flex;
  gap: 5px;
}

.arrow {
  width: 20px;
  height: 20px;
  border-top: 4px solid white;
  border-right: 4px solid white;
}

.header-nav__arrow-left {
  transform: rotate(-135deg);
}

.header-nav__arrow-right {
  transform: rotate(45deg);
}

.header-nav__arrow-line {
  transform: rotate(2deg);
  color: rgb(230, 230, 230);
}

.services {
  background: rgb(247, 247, 247);
}

.container-servicers {
  text-align: center;
  padding-top: 80px;
}
@media screen and (max-width: 999px) {
  .container-servicers {
    padding-top: 40px;
  }
}

.services-title {
  margin-bottom: 10px;
  font-size: 28px;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
  color: black;
}

.services-line {
  display: inline-block;
  width: 50px;
  height: 3px;
  margin-bottom: 24px;
  background: rgb(79, 143, 240);
}

.services-text {
  padding-bottom: 24px;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}

.content-services__container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 38px;
}
@media screen and (max-width: 999px) {
  .content-services__container {
    display: flex;
    flex-wrap: nowrap;
  }
}

.content-services {
  background: rgb(255, 255, 255);
}

.content-services__frame {
  width: 360px;
  height: 250px;
  text-align: center;
  padding: 50px;
}

.content-services__frame-img {
  margin-bottom: 18px;
}

.content-services__frame-title {
  font-size: 20px;
  font-weight: 800;
  margin-bottom: 10px;
}

.content-services__frame-text {
  font-size: 14px;
  font-weight: 400;
}

.content-services:hover {
  color: white;
  background: rgb(79, 143, 240);
}

@media screen and (max-width: 999px) {
  .btn-wrapper {
    padding-bottom: 40px;
  }
}

